import { Affix, Row, Col, Button, Layout, message, Grid, Drawer } from 'antd';
import React, { useEffect, useState, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import _ from 'lodash';
import AppHeader from './AppHeader';
import AppDrawer from './AppDrawer';
import AppFooter from './AppFooter';
const { useBreakpoint } = Grid;

const AppLayout = ({ children }) => {
	const blockchain = useSelector((state) => state.blockchain);
	const screens = useBreakpoint();
	const isMobile = screens.xs;
	const [visible, setVisible] = useState(false);

	useEffect(() => {
		if (blockchain.errorMsg) {
			console.log(blockchain);
			message.error(blockchain.errorMsg);
		}
	}, [blockchain.errorMsg]);

	return (
		<Layout.Content className={`dark ${isMobile ? 'mobile' : ''}`}>
			<AppDrawer setVisible={setVisible} visible={visible} />
			<AppHeader setVisible={setVisible} />
			<Layout.Content style={{ minHeight: '70vh' }} className='dark'>
				<Row justify="center">
					<Col span={20}>{children}</Col>
				</Row>
				
			</Layout.Content>
			<AppFooter />
		</Layout.Content>
	);
};

export default AppLayout;
