import { Affix, Row, Col, Button, Layout, Grid } from 'antd';
import React, { useEffect, useState, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import _ from 'lodash';
import { MenuUnfoldOutlined } from '@ant-design/icons';
import { connect } from '../redux/blockchain/blockchainActions';

const { useBreakpoint } = Grid;

const AppHeader = ({ setVisible }) => {
	const dispatch = useDispatch();
	const blockchain = useSelector((state) => state.blockchain);
	const screens = useBreakpoint();
	const isMobile = screens.xs;

	return (
		<Affix>
			<Layout.Header
				className='dark'
				style={{
					lineHeight: 1.5,
					// background: '#fff',
					height: isMobile ? 80 : 80,
					padding: isMobile ? '10px 30px' : '0 50px',
					boxShadow: 'rgba(0, 0, 0, 0.8) 0px 3px 12px',
				}}
			>
				{isMobile ? (
					<Row
						align='middle'
						justify='space-between'
						style={{ height: isMobile ? 60 : 80 }}
					>
						<Col>
							<MenuUnfoldOutlined
								onClick={() => setVisible(true)}
								style={{ fontSize: 30 }}
							/>
						</Col>
						<Col>
							<Button
								className='dark-button'
								onClick={() => {
									if (!blockchain.account) {
										dispatch(connect());
									}
								}}
								style={{
									border: 'none',
									borderRadius: 10,
									height: 40,
									width: 140,
									fontSize: 14,
									fontWeight: 'bold',
								}}
							>
								{blockchain.account ? (
									<div
										style={{
											width: '100%',
											textOverflow: 'ellipsis',
											overflow: 'hidden',
											whiteSpace: 'nowrap',
										}}
									>
										{blockchain.account.substring(0, 6)}...{' '}
										{blockchain.account.substring(
											blockchain.account.length - 6,
											blockchain.account.length
										)}
									</div>
								) : (
									'Connect Wallet'
								)}
							</Button>
						</Col>
					</Row>
				) : (
					<Row
						justify='space-between'
						align='middle'
						style={{ margin: '0px 30px', height: isMobile ? 60 : 80 }}
					>
						<Col span={8}>Logo</Col>
						<Col span={blockchain.account ? 14 : 12}>
							<Row
								justify='space-between'
								align='middle'
								style={{ fontSize: 16 }}
							>
								<Col>
									<Link to='/' className='white-high'>
										Home
									</Link>
								</Col>
								<Col>
									<Link
										to={{
											pathname: '/',
											state: { section: 'about' },
										}}
										className='white-high'
									>
										About
									</Link>
								</Col>
								<Col>
									<Link
										to={{
											pathname: '/airdrop',
										}}
										className='white-high'
									>
										Airdrop
									</Link>
								</Col>
								<Col>
									<a
										href='https://testnets.opensea.io/collection/crypto-wallstreetbets'
										className='white-high'
										target='_blank'
										rel='noreferrer'
									>
										All Collections
									</a>
								</Col>
								<a href='/#faq' className='white-high'>
									FAQ
								</a>
								{blockchain.account && (
									<Col>
										<Link to='/account' className='white-high'>
											My Collections
										</Link>
									</Col>
								)}
								<Col>
									<Button
										className='dark-button'
										onClick={() => {
											if (!blockchain.account) {
												dispatch(connect());
											}
										}}
										style={{
											border: 'none',
											borderRadius: 10,
											height: 50,
											width: 160,
											fontWeight: 'bold',
										}}
									>
										{blockchain.account ? (
											<div
												style={{
													width: '100%',
													textOverflow: 'ellipsis',
													overflow: 'hidden',
													whiteSpace: 'nowrap',
												}}
											>
												{blockchain.account.substring(0, 6)}...{' '}
												{blockchain.account.substring(
													blockchain.account.length - 6,
													blockchain.account.length
												)}
											</div>
										) : (
											'Connect Wallet'
										)}
									</Button>
								</Col>
							</Row>
						</Col>
					</Row>
				)}
			</Layout.Header>
		</Affix>
	);
};

export default AppHeader;
