import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Home from './Home'
import Airdrop from './Airdrop'
import Account from './Account'

export default function App() {
  return (
    <Router>
        <Switch>
          <Route path="/account">
            <Account />
          </Route>
          <Route path="/airdrop">
            <Airdrop />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>
    </Router>
  );
}
